<template>
    <el-container>
        <el-header>
            <el-button type="primary" @click="add()" icon="el-icon-news">新增</el-button>
        </el-header>
        <el-main>
            <el-row>
                <el-col>
                    <el-table
                            :data="tableData"
                            border
                            stripe
                            style="width: 100%;">

                        <el-table-column
                                label="Banner">
                            <template slot-scope="scope">
                                <img :src="scope.row.url" style="max-height: 108px">
                            </template>
                        </el-table-column>

                        <el-table-column
                                prop="sortIndex"
                                width="80"
                                label="排序号">
                        </el-table-column>

                        <el-table-column
                                label="操作"
                                width="185">
                            <template slot-scope="scope">
                                    <el-button @click="editHandleClick(scope.row.id)" size="mini" type="primary"
                                               icon="el-icon-edit">
                                        编辑
                                    </el-button>
                                    <el-button @click="deleteHandleClick(scope.row.id)" type="danger" size="mini"
                                               icon="el-icon-delete">
                                        删除
                                    </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row>
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="pageSize"
                        layout="total, prev, pager, next"
                        :total="countSize">
                </el-pagination>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
    export default {
        data() {
            return {
                tableData: null,
                currentPage: 1,
                countSize: 0,
                pageSize: 1
            }
        },
        mounted() {
            this.reload();
        },
        methods: {
            add() {
                this.$router.push({path: '/bannerManage/add', replace: true});
            },
            editHandleClick(id) {
                this.$router.push({path: '/bannerManage/add', query: {id: id}, replace: true});
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.reload();
            },
            reload() {
                this.axios.get("/api/banner/page?page=" + this.currentPage).then((response) => {
                    this.tableData = response.data.data.content;
                    this.currentPage = response.data.data.number + 1;
                    this.countSize = response.data.data.totalElements;
                    this.pageSize = response.data.data.size;
                }).catch((response) => {
                    console.log(response);
                })
            },
            deleteHandleClick(id) {
                this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确认',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.post('/api/banner/delete/' + id).then((response) => {
                        if (response.data.state == '0') {
                            //刷新数据
                            this.reload();
                        } else {
                            this.$message({
                                type: 'error',
                                message: '删除失败'
                            });
                        }
                    }).catch((response) => {
                        console.log(response);
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        }
    }
</script>

